div.sec {
  border-radius: 5px;
  padding: 10px;
  position: relative;
  border: 1px solid darkgray;
}
div.sec hr {
  height: 1px;
  border: 0px;
  border-top: 1px solid lightgray;
}
div.sec .title {
  font-size: large;
  text-align: center;
}
div.sec .headerRightContainer {
  display: flex;
  justify-content: flex-end;
  position: absolute;
  top: 10px;
  right: 12px;
  gap: 8px;
}

