.trackerContainer {
  display: grid;
  grid-template-columns: minmax(265px, max-content);
  row-gap: 10px;
  margin: 10px;
  transform-origin: 0 0;
  scale: 1.55;
}
.trackerContainer .trackedClassContainer {
  display: grid;
  grid-template-columns: [turn-complete] max-content [info] 1fr [initiative] max-content [move-buttons] max-content [delete] max-content;
  place-items: center;
}
.trackerContainer .roundActionsOuter {
  display: flex;
  gap: 10px;
  justify-content: center;
}
.trackerContainer .roundActionsOuter .fa-play {
  color: green;
}
.trackerContainer .roundActionsOuter .fa-rotate {
  color: steelblue;
}
.trackerContainer .noFiguresPlaceholder {
  text-align: center;
  font-size: large;
  font-weight: bold;
}
.trackerContainer .noFiguresPlaceholder .fa-triangle-exclamation {
  color: #e6e20e;
  padding-right: 6px;
  -webkit-text-stroke: 1px black;
}
.trackerContainer .separator {
  grid-column: 1/-1;
  height: 8px;
}

