.figureAdderOuter {
  display: grid;
  grid-template-columns: max-content;
  row-gap: 10px;
}
.figureAdderOuter .figureAdderInput {
  margin-right: 10px;
}
.figureAdderOuter .figureAdderSelect {
  height: 22px;
}
.figureAdderOuter .figureAdderNameField {
  width: 100px;
}
.figureAdderOuter .rowSpacer {
  margin-top: 4px;
}
.figureAdderOuter .fa-plus {
  color: darkgreen;
  font-size: large;
}

